import React, {useState, useEffect} from 'react'
import hero2 from './hero2.png'
import Calendar from 'react-calendar';
import 'react-calendar/dist/Calendar.css';
import moment from 'moment'



function Contact() {
  const [calValue, setCalValue] = useState(new Date());
  const [party, setParty] = useState(0);
  const [time, setTime] = useState('5:00');
  const [reso, setReso] = useState();
  const [resoModal, setResoModal] = useState(false);

  function onChange(nextValue) {
    setCalValue(nextValue);
  }
  function timeChange(time) {
    setCalValue(time);
     

  }
  
  function resoClick() {
    setReso(true)
    setResoModal(true)
  }


  
    return (
			<div className="menuModal2" >
				<section classname="about">

					<section className="reservation">
       
						<h1 style={{fontSize:'3rem'}}>Book A Table</h1>
						<Calendar onChange={onChange} value={calValue} />
            
                <select style={{ height: "5vh", width: "6vw", padding:'5px'}}  name="Time"  onChange = {({ target: { value } }) => {setTime(value);}}  >
                  <option  value="">Time</option>
                    <option value="5:00pm">5:00pm</option>
                    <option value="5:30pm">5:30pm</option>
                    <option value="6:00pm">6:00pm</option>
                    <option value="6:30pm">6:30pm</option>
                    <option value="7:00pm">7:00pm</option>
                    <option value="7:30pm">7:30pm</option>
                    <option value="8:00pm">8:00pm</option>
                    <option value="8:30pm">8:30pm</option>
                    <option value="9:00pm">9:00pm</option>
                    <option value="9:30pm">9:30pm</option>
                    <option value="10:00pm">10:00pm</option>
                    <option value="10:30pm">10:30pm</option>
                  </select>
              </section>



              <section>
            <select style={{ height: "5vh", width: "6vw",position:'relative',bottom:'47px', right:'130px' }} name="Time"   onChange = {({ target: { value } }) => {setParty(value);}} >
                  <option  value="">Party Size</option>
                    <option value="1">1</option>
                    <option value="2">2</option>
                    <option value="3">3</option>
                    <option value="4">4</option>
                    <option value="5">5</option>
                    <option value="6">6</option>
                    <option value="7">7</option>
                    <option value="8">8</option>
                    <option value="9">9</option>
                    <option value="10">10+</option>
      
                  </select>
                  <button className="requestBut" onClick={resoClick}>Request Table</button>
                  <p>Your reservation request  is for <b>{moment(calValue).format('MMMM Do YYYY')} at {time} for a party of {party}.</b></p>
              </section>
           
            </section>

				<h1>Questions? Reach us at 555-5555.</h1>
        <h2>Or send us a message below.</h2>

				<form className="contact__form">
					<input
						type="text"
						name="name"
						placeholder="name"
						title="Your name"
						maxLength="50"
						required
					/>
					<input
						type="email"
						name="email"
						placeholder="email"
						title="Your email"
						maxLength="50"
						required
					/>
					<textarea
						type="text"
						name="message"
						placeholder="message"
						title="Your message"
						maxLength="550"
						required
					/>
					<input style={{position:'relative', left:'6.8vw'}} type="submit" value="send message" />
				</form>
        <section className="resoConfirm" style={{ display: resoModal ? "flex" : "none"  }} >

          <p>Your table has been confirmed for {moment(calValue).format('MMMM Do YYYY')} at {time} for a party of {party}.</p>
          <button onClick={ ()=> setResoModal(false)}>Okay</button>
  </section>
			</div>
		);
}

export default Contact
