import React, {useState} from 'react'
import pho2 from './pho2.jpg'; import bahn from './bahn.jpg';import xeo from './xeo.jpg';import kapow from './kapow.jpg';
import kacho from './kacho.jpg';import pine from './pine.jpg';import padse from './padse.jpg';import bas from './bas.jpg';
import red from './red.jpg';import pt from './pt.jpg';
import hue from './hue.jpg';
import buncha from './buncha.jpg'
import ShoppingCartIcon from "@material-ui/icons/ShoppingCart";
const recipes = [
       
    {id:1, img: bahn, protein: "" , name:"Bánh mì", ingredients: ["Pork belly, fish cakes, meatballs, and the very necessary pickled carrots, on a French Baguette"], price: 7.50},
    {id:2, img: pho2, name:"Phở", ingredients: ["Assortment of meat, bean sprouts, lime wedges, basil, mint, cilantro, onions,chili sauce, & fish sauce "], price:9.50},
    {id:3, img: buncha, name: "Bún chả", ingredients: [ "Grilled pork sausage patties, a basket of herbs, bean sprouts, pickled veggies, and, once again, the ever-important nước chấm sauce"], price : 8.00 },
    {id:4, img: kacho, name:"Ca Kho To (Caramelized Fish in Clay Pot)", ingredients: ["In this dish, the fish develops a sweet-savory gooeyness from the sugar and fish sauce over the course of the long braise"], price:7.50},
    {id:5, img: xeo,  name:"Bánh xèo", ingredients: ["The batter is made with rice flour, coconut milk, and turmeric (hence the nice golden-yellow hue) and is pan-fried altogether with pork, shrimp, and a heap of bean sprouts. Wrap up the pancake with lettuce and herbs. "], price: 13.50},
    {id:6, img: kapow, name: "Pad Kapow", ingredients: [ "Thai basil stir-fry. Your choice of meat is stir-fried with fresh mushrooms, onions, and bell peppers in a sweet basil sauce."], price : 9.50 },
    {id:7,img: red,  name:"Red Curry Dinner", ingredients: ["Bamboo, onions, and red peppers are cooked in red curry and coconut milk, then served with your choice of meat. Gluten friendly."], price:7.50},
    {id:8, img: hue,  name:"Bun Bo Hue", ingredients: ["Assortment of meat, bean sprouts, lime wedges, basil, mint, cilantro, onions,chili sauce, & fish sauce, in a deep red broth "], price:10.50},
    {id:9,img: pine,  name: "Pineapple Fried Rice", ingredients: [ "Your choice of white or brown rice, stir-fried with pineapple, egg, red onions, bell peppers, and butter. Gluten free soy sauce available."], price : 8.00},
    {id:10,img: padse,  name:"Pad See-Ew", ingredients: ["Your choice of meat stir-fried with fresh flat noodles, eggs, sliced vegetables, sesame oil, and sweet soy sauce."], price:13.50},
    {id:11, img: bas, name:"Basil Noodle Dinner", ingredients: ["Your choice of meat stir-fried with mixed vegetables in a sweet basil sauce, served on a nest of crispy egg noodles.. "], price:8.50},
    {id:12, img: pt,  name: "Pad Thai", ingredients: [ "Famous Thai-style stir fry rice noodles with egg, peanuts, sprouts, scallions, and Thai seasoning."], price : 9.00 }
  ];
  const extras = [
       
    {protein:"No Protein Choice", spice:"Default Spice", info:"No Additional Info"},
  ]
  let currentOrder = [
  ];


function Order() {

      const [food, setFood] = useState(recipes);
      const [extra, setExtra] = useState(extras);
      const [clickFood, setClickFood] = useState([]); 
      const [clickPrice, setClickPrice] = useState([]);
      const [modal, setModal] = useState(false);
      const [order, setOrder] = useState([]);
      const [tax, setTax] = useState(0);
      const [price, setPrice] = useState(0);

      function itemClick(meal) {
       setModal(true);
       setTax(current => (current += (meal.price *.0875)  ));
       setTax(current => Math.round(current * 100)/100);
       tax.toFixed(2);
       price.toFixed(2)
       setPrice(current => (current += (meal.price +(meal.price *.0875)))  );
       setPrice(current => Math.round(current * 100)/100) ;
       
       setClickFood([meal.name, meal.price, meal.id, ]) ;
       currentOrder.push({id:Math.floor(Math.random() * 1100) , item: meal.name, price: meal.price, protein: extra});
       setOrder(currentOrder);       
        console.log(clickFood)
       if (price < 0){
         setPrice(0)
       }
     
      }
      const deleteItem = (id, price) => {       
        setTax(current => (current -= (price *.0875)  ));
        setTax(current => Math.round(current * 100)/100);
        setPrice(current => (current -= (price )  ));
        setPrice(current => current -= tax);
        setPrice(current => Math.round(current * 100)/100);
        setOrder(order.filter((order) => order.id !== id));

      }

      const extraClick = (e) => {       
        console.log(e)
        setExtra(e)
       

      }

      currentOrder = order;

      if (price < 0){
        setPrice(0)
      }
   
    return (
			<div className="Menu">
        <div className="foodCart">
			<ShoppingCartIcon/> <h5>{order.length}</h5>
      </div>
				<h1 style={{ color: "red", position: "relative", right: "20vw" }}>
					Main Dishes 
				</h1>
     
				<section className="appMenu">
					<h5 className="menugrid">
						{" "}
						{food.map((meal) => (
							<div  style={{width:'99%', maxHeight:'59vh', borderRadius:'12px',margin:'6px'}} className="menuItem" onClick={() => itemClick(meal)}>
              <img style={{width:'98%', maxHeight:'15vh', borderRadius:'12px'}}src={meal.img} />
								<h3
									className="name"
									style={{ color: "red" }}
									key={meal.id + Math.floor(Math.random() * 100)}
								>
									{meal.name}
								</h3>
								<h4 key={meal.ingredients}>{meal.ingredients}</h4>
            
								<h4 key={[meal.price]}>${meal.price.toFixed(2)}</h4>
							</div>
						))}
					</h5>

					<section className="appOrder">
						<div className="orderContainer">
							<h1 style={{borderBottom:'2px solid black', padding:'12px'}}> Your Order</h1>

							{order.map((order) => (
								<div buttonAdd>
									<h3 key={[order]} style={{ color: "red" }}>
										{order.item}  ${order.price.toFixed(2)}
										<span
											className="delItem"
											onClick={() => deleteItem(order.id, order.price)}
										>
											{" "}
											x
                    
                      {extra.map((ex) => (                       
                          <h3
                          >
                            {ex.protein}
                          </h3>
                      ))}
           
										</span>
       
									</h3>
								</div>
							))}
							<h3 style={{borderTop:'2px solid black', padding:'12px'}}>Tax: ${tax.toFixed(2)}</h3>
							<h3>Total: ${price.toFixed(2)}</h3>

							<button
								style={{
									backgroundColor: "red",
									color: "white",
									height: "24px",
									width: "16vw",
									borderRadius: "12px",
								}}
							>
								Checkout
							</button>
						</div>
					</section>

					<section
						className="menuModal"
						style={{ display: modal ? "block" : "none" }}
					>
						<div className="modalContainer">
							<h1>
								{" "}
								{clickFood[0]}{" "}
                
								<span
									className="cancel"
									style={{ fontSize: "2em", color: "red", cursor: "pointer" }}
									onClick={() => setModal(false)}
								>
									x
								</span>{" "}
							</h1>
							<h4>Protien</h4>
							<select  onChange={ (e)=> extraClick([{protein: e.target.value}])} value="" style={{ height: "5vh", width: "6vw" }} name="Protien"   >
              <option  value="">Select</option>
								<option value="Beef">Beef</option>
								<option value="Pork">Pork</option>
								<option value="Chicken">Chicken</option>
								<option value="Tofu">Tofu</option>
							</select>
							<h4>Spice</h4>
							<select style={{ height: "5vh", width: "6vw" }} name="Heat"  >
              <option value="">Select</option>
								<option value="Mild">Mild</option>
								<option value="Medium">Medium</option>
								<option value="Hot">Hot</option>
								<option value="Xtra Hot">Xtra Hot! </option>
							</select>
							<textarea
								placeholder="Any additional information we should know. Allergies, dietary restrictions, etc"
								rows={5}
								cols={5}
							/>
							<button
								style={{ backgroundColor: "red", color: "white" }}
								className="modalBut"
								onClick={() =>  setModal(false)}
							>
								Add to cart
							</button>
      
						</div>
					</section>
				</section>
			</div>
		);
}

export default Order